.NavbarItems {
    background: #ffffff;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    /*margin-top: -80px;*/
    top: 0;
    z-index: 999;
}

.mobile-nav {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
}

.navbar-logo-mobile {
    display: none;
}

.navbar-logo {
    font-family: 'Neucha', cursive;
    color: black;
    justify-self: start;
    margin-left: 4vw;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    height: 60px;
    margin-top: 30px;
    font-weight: 600;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(6, auto);
    grid-gap: 5px;
    list-style: none;
    text-align: center;
    width: 90vw;
    justify-content: end;
    margin-right: 2vw;
}

.nav-links {
    color: black;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    background-color: rgba(104, 136, 142, 0.3);
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

@media screen and (max-width:600px) {
    .NavbarItems {
        background: #ffffff;
        margin-top: 5px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.2rem;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 999;
    }

    .mobile-nav {
        position: -webkit-sticky;
        position: sticky;
        top: 0;
        z-index: 999;
    }

    .navbar-logo-mobile {
        display: flex;
        font-family: 'Neucha', cursive;
        color: black;
        justify-content: center;
        cursor: pointer;
        text-decoration: none;
        font-size: 2rem;
        padding-top: 10px;
        height: 40px;
        font-weight: 600;
    }

    .navbar-logo {
        display: none;
    }

    .nav-menu {
        display: grid;
        grid-template-columns: repeat(4, auto);
        list-style: none;
        text-align: center;
        width: 100vw;
        justify-content: center;
    }
}
