:root {
    --primary: #68888e;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border: none;
    cursor: pointer;
    margin-right: 5px;
}

.btn:hover {
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #68888e;
    transition: 250ms;
}

.btn--primary {
    background-color: #68888e;
    background-color: var(--primary);
}

.btn--outline {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
}

.btn--signup {
    background-color: #023246;
    color: white;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
}

.btn--signup:hover {
    padding: 8px 20px;
    transition: all 0.3s ease-out;
    background: #fff;
    color: #023246;
}

.btn--medium {
    padding: 8px 20px;
    border-radius: 4px;
    font-size: 18px;
    color: #fff;
}

.btn--large {
    padding: 12px 26px;
    border-radius: 4px;
    font-size: 20px;
    color: #fff;
}
